import React from "react";
import "./Chart.css";
import Highcharts from "highcharts/highstock";
import HighchartsReact from "highcharts-react-official";
import {
  ftse100,
  ftse250,
  ftse350,
  ftseAllShare,
  ftseSmallCap,
  ftseAim100,
  ftseTechMARK100,
  DowJones,
  dax,
  cac40,
  HangSeng,
  ibex,
  nasdaq,
  nikkei,
} from "../../constants";

const Chart = (index) => {
  var indexpintar = "ftse100";
  var data = ftse100;
  if (index.id != undefined) {
    indexpintar = index.id;
  }

  switch (indexpintar) {
    case "ftse250":
      data = ftse250;
      break;
    case "ftse350":
      data = ftse350;
      break;
    case "ftseAllShare":
      data = ftseAllShare;
      break;
    case "ftseAllShare":
      data = ftseAllShare;
      break;
    case "ftseSmallCap":
      data = ftseSmallCap;
      break;
    case "ftseAim100":
      data = ftseAim100;
      break;
    case "ftseTechMARK100":
      data = ftseTechMARK100;
      break;
    case "DowJones":
      data = DowJones;
      break;
    case "dax":
      data = dax;
      break;
    case "cac40":
      data = cac40;
      break;
    case "HangSeng":
      data = HangSeng;
      break;
    case "ibex":
      data = ibex;
      break;
    case "nasdaq":
      data = nasdaq;
      break;
    case "nikkei":
      data = nikkei;
      break;
    default:
      data = ftse100;
  }

  const options = {
    scrollbar: {
      hide: true,
    },
    series: [
      {
        name: indexpintar,
        data: data,
      },
    ],
  };

  return (
    <div className="divShares2">
      <HighchartsReact
        highcharts={Highcharts}
        constructorType={"stockChart"}
        options={options}
        containerProps={{ style: { height: "100%" } }}
      />
    </div>
  );
};
export default Chart;
