import React from "react";
import SearchSingleResult from "../SearchSingleResult";

const SearchResults = ({ listOfResults = [] }) => {
  listOfResults = listOfResults.slice(0, 5);

  return (
    <>
      {listOfResults.length
        ? listOfResults.map((Result) => (
            <SearchSingleResult {...Result}></SearchSingleResult>
          ))
        : ""}
    </>
  );
};

export default SearchResults;
