import React, { useState } from "react";
import "./SearchBox.css";
import MaterialIcon from "material-icons-react";
import { companies } from "../../constants";
import SearchResults from "../SearchResults";

const SearchBox = () => {
  const [fits, setFits] = useState([]);

  const onChange = (event) => {
    //Get Entered Data
    setFits(
      companies.filter((company) => {
        const regex = new RegExp(`^${event.target.value}`, "gi");
        return company.Company_Name.match(regex);
      })
    );
    if (event.target.value.length === 0) {
      setFits([]);
    }
  };

  const onClick = (event) => {
    document.getElementById("search").value = "";
    setFits([]);
  };

  return (
    <div className="h1side">
      <button>
        <MaterialIcon icon="search" />
      </button>
      <div className="buscadorYresultados">
        <input
          type="text"
          className="buscador"
          autoComplete="off"
          id="search"
          name="q"
          placeholder="Enter stock name or EPIC"
          onChange={onChange}
        />
        <div className="companyList" id="companyList" onClick={onClick}>
          <SearchResults listOfResults={fits} />
        </div>
      </div>
    </div>
  );
};

export default SearchBox;
