import React from "react";
import "./SearchSingleResult.css";
import { NavLink } from "react-router-dom";

const SearchSingleResult = (Result = []) => {
  var url = "/shares?share=" + Result.Company_Name;

  return (
    <div className="row">
      <NavLink to={url}>
        {Result.Company_Name}({Result.ICB_Industry})
      </NavLink>
    </div>
  );
};
export default SearchSingleResult;
